.hero__img {
    object-fit: cover;
    width: 100%;
    height: 880px;

    @media (max-width: $vp-1023) {
        height: 640px;
    }

    @media (max-width: $vp-767) {
        height: 560px;
    }
}

.hero__container {
    width: 100%;
    height: 100%;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
    grid-auto-flow: row;
    background-color: $black-pearl;
}

.hero__img-container {
    grid-column: 1 / -1;
    grid-row: 1 / 2;
}

.hero__text-wrapper {
    grid-column: 1 / -1;
    grid-row: 1 / 2;
    padding-left: 125px;
    padding-left: 25%;
    padding-top: 315px;
    padding-right: 50px;

    @media (max-width: $vp-1023) {
        padding-top: 180px;
        padding-left: 50px;
        padding-left: 10%;
    }

    @media (max-width: $vp-767) {
        padding-top: 150px;
        padding-left: 25px;
    }
}

.hero__title {
    font-family: $roboto;
    font-weight: 700;
    font-size: 80px;
    line-height: 94px;
    letter-spacing: 0.07em;
    text-transform: uppercase;
    color: $color-alice-blue;
    margin-bottom: 40px;
    margin-top: 0;

    @media (max-width: $vp-1023) {
       font-size: 64px;
       line-height: 75px;
       letter-spacing: 0.07em;
    }

        @media (max-width: $vp-767) {
            font-size: 36px;
            line-height: 42px;
            letter-spacing: 0.07em;
            text-transform: uppercase;
        }
}

.hero__text {
    font-family: $mono;
    font-weight: 400;
    font-size: 22px;
    line-height: 155%;
    color: $color-alice-blue;
    margin-bottom: 40px;
    margin-top: 0;

    @media (max-width: $vp-1023) {
        font-size: 22px;
        line-height: 155%;
     }

     @media (max-width: $vp-767) {
        font-size: 14px;
     }
}

.hero__button {
    font-family: $roboto;
    font-weight: 700;
    font-size: 18px;
    line-height: 21px;
    text-align: center;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    padding: 15px 25px;
    color: $color-alice-blue;
    border-radius: 3px;
    background-color: $deep-sky-blue;
    border: none;
    margin-top: 0;
    cursor: pointer;

    @media (max-width: $vp-1023) {
            font-size: 18px;
            line-height: 21px;
            text-align: center;
            letter-spacing: 0.05em;
        }

        @media (max-width: $vp-767) {
            font-size: 14px;
            font-size: 14px;
            line-height: 16px;
            letter-spacing: 0.05em;
         }
}

.hero__button:hover {
    opacity: 0.8;
}

.hero__button:focus {
    opacity: 0.8;
}

.hero__button:active {
    opacity: 0.6;
}


